import React from 'react';
import s from './login-form.module.css';
import {NavLink} from "react-router-dom";

const LoginForm = () => {
    return (
        <div className={s.header_login}>
            <NavLink to="/authorization"  className={s.header_login_enter}></NavLink>
            <NavLink to="/registration"className={s.header_login_authorization}></NavLink>
        </div>
    )
};

export default LoginForm;